import gql from 'graphql-tag'

export const Create_Pack = gql`
  mutation createPack($input: PackWebInput, $file: Upload) 
  {
    createPack(input: $input, file: $file)
    {
        id
        name
        price
    }
  }
`

export const Update_Pack = gql`
  mutation updatePack($input: PackWebInput, $file: Upload) 
  {
    updatePack(input: $input, file: $file)
    {
      id
      name
    }
  }
`

export const Delete_Pack = gql`
  mutation deletePack($id:ID) {
    deletePack(id: $id) 
  } 
`

export const Restore_Pack = gql`
  mutation restorePack($id:ID) {
    restorePack(id: $id) 
  } 
`