import gql from 'graphql-tag'


export const Packs_Query = gql`
    query getProducts($start: Int, $end: Int) {
        getProducts(start: $start, end: $end) {
            id
            name
            price
            quantity
            unitOfmeasurement {
                id
                name
                abbreviation
            }  
        }
    }
`
export const Packs_Paginate = gql`
    query getPacks($start: Int, $end: Int) {
        getPacks(start: $start, end: $end) {
            id
            name
            price
            date
            status
            quantity
            pack_products {
                id
                quantity
                product {
                  id
                  name
                  price
                }
            }
        },
        totalPacks
    }
`