import React, { useState } from 'react';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/styles';
import { useLazyQuery } from '@apollo/react-hooks';
import { Products_By_Name } from '../../queries/product.query'
import PropTypes from 'prop-types';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import {
  Button,
  Divider,
  Drawer,
  TextField
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column'
  },
  drawer: {
    width: 620,
    maxWidth: '100%'
  },
  header: {
    padding: theme.spacing(2, 1),
    display: 'flex',
    justifyContent: 'space-between'
  },
  buttonIcon: {
    marginRight: theme.spacing(1)
  },
  content: {
    padding: theme.spacing(0, 3),
    flexGrow: 1
  },
  contentSection: {
    padding: theme.spacing(2, 0)
  },
  contentSectionHeader: {
    display: 'flex',
    justifyContent: 'space-between',
    cursor: 'pointer'
  },
  contentSectionContent: {},
  formGroup: {
    padding: theme.spacing(2, 0)
  },
  fieldGroup: {
    display: 'flex',
    alignItems: 'center'
  },
  field: {
    marginTop: 0,
    marginBottom: 0
  },
  flexGrow: {
    flexGrow: 1
  },
  addButton: {
    marginLeft: theme.spacing(1)
  },
  tags: {
    marginTop: theme.spacing(1)
  },
  minAmount: {
    marginRight: theme.spacing(3)
  },
  maxAmount: {
    marginLeft: theme.spacing(3)
  },
  radioGroup: {},
  actions: {
    padding: theme.spacing(3),
    '& > * + *': {
      marginTop: theme.spacing(2)
    }
  }
}));

const FilterSearchProduct = props => {
  const { open, onClose, onFilter, selectProduct, getProducts, className, ...rest } = props;
  const [products, setProducts] = useState([])
  const [name, setName] = useState('')

  const classes = useStyles();


  const onChangeName = event => {

    setName(event.target.value)
    if (event.target.value.toString().trim().length > 0)
      getProductsByName({ variables: { name: event.target.value, products: props.getProducts() }, fetchPolicy: "no-cache" })
    else
      setProducts([])
  }
  const handleClear = () => {
    setName('')
    setProducts([])
  }
  const [getProductsByName, { data, loading }] = useLazyQuery(Products_By_Name, {
    fetchPolicy: "no-cache",
    onCompleted: () => {

      if (loading)
        console.log("Cargando ")
      else {
        console.log(data)
        setProducts(data.getProductsByName)
      }

    }
  })

  const onSelectProduct = (product, index) => {

    let aux = products
    aux.splice(index, 1)

    setProducts([...aux])
    props.selectProduct(product)
  };


  return (
    <Drawer
      anchor="right"
      classes={{ paper: classes.drawer }}
      onClose={onClose}
      open={open}
      variant="temporary"
    >
      <form
        {...rest}
        className={clsx(classes.root, className)}
      >
        <div className={classes.header}>
          <Button
            onClick={onClose}
            size="small"
          >
            <CloseIcon className={classes.buttonIcon} />
            Close
          </Button>
        </div>
        <div className={classes.content}>
          <div className={classes.contentSection}>

            <Divider />


            <div className={classes.formGroup}>
              <TextField
                className={classes.field}
                fullWidth
                label="Nombre del producto"
                autoComplete="off"
                margin="dense"
                name="name"
                autoFocus={true}
                onChange={onChangeName}
                value={name}
                variant="outlined"
              />
            </div>
            <div className={classes.formGroup}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Producto</TableCell>
                    <TableCell>Precio</TableCell>
                    <TableCell>Cantidad</TableCell>
                    <TableCell>Agregar</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {
                    products.map((product, index) => (
                      <TableRow key={index}>
                        <TableCell>{product.name}</TableCell>
                        <TableCell>{product.price}</TableCell>
                        <TableCell>{product.quantity}</TableCell>

                        <TableCell>
                          <Button
                            color="primary"
                            size="small"
                            variant="outlined"

                            onClick={() => {
                              onSelectProduct(product, index)
                            }}
                          >
                            Agregar
                                </Button>
                        </TableCell>

                      </TableRow>
                    ))
                  }
                </TableBody>
              </Table>
            </div>
          </div>

        </div>
        <div className={classes.actions}>
          <Button
            fullWidth
            onClick={handleClear}
            variant="contained"
          >
            <DeleteIcon className={classes.buttonIcon} />
            Limpiar
          </Button>
        </div>
      </form>
    </Drawer>
  );
};
export default FilterSearchProduct;
