import gql from 'graphql-tag'


export const Products_Query = gql`
    query getProducts($start: Int, $end: Int) {
        getProducts(start: $start, end: $end) {
            id
            name
            price
            quantity
        }
    }
`

export const Products_Paginate = gql`
    query getProducts($start: Int, $end: Int) {
        getProducts(start: $start, end: $end) {
            id
            name
            price
            quantity
            status
        },
        totalProducts
    }
`
export const Products_By_Name = gql`
    query getProductsByName($name: String, $products: [String]) {
        getProductsByName(name: $name, products: $products) {
            id
            name
            price
            quantity
            status
        }
    }
`