import React, { useState, Fragment, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import { Packs_Paginate } from '../../queries/pack.query'
import { Delete_Pack, Restore_Pack } from '../../mutations/Pack/packs'
import { useMutation } from "@apollo/react-hooks"
import { useLazyQuery } from '@apollo/react-hooks';

import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import RestoreIcon from '@material-ui/icons/Restore';
import Tooltip from '@material-ui/core/Tooltip';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import { esES } from '@material-ui/core/locale';
import Button from '@material-ui/core/Button';

import CreatePack from './CreatePack';
import EditPack from './EditPack';


import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
const theme = createMuiTheme({}, esES);

const useStyles = makeStyles({
    fragment: {
        padding: 40
    },
    margin: {
        margin: theme.spacing(1),
    },
    root: {
        width: '100%',
        margin: theme.spacing(1),
    },
    container: {
        maxHeight: 440,
    },
    iconWidth: {
        width: 10
    },
})

export default function Packs() {

    const classes = useStyles()
    const [page, setPage] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(10)
    const [totalFields, setTotalFields] = useState(0)

    const [deletePack] = useMutation(Delete_Pack)
    const [restorePack] = useMutation(Restore_Pack)

    const [openCreatePack, setOpenCreatePack] = React.useState(false)
    const [openRestorePack, setOpenRestorePack] = useState(false)
    const [openDeletePack, setOpenDeletePack] = useState(false)
    const [openEditPack, setOpenEditPack] = useState(false)
    const [pack, setPackSelected] = useState({})
    const [packs, setPacks] = useState([])

    useEffect(() => {
        getPacks({ variables: { start: page, end: rowsPerPage } })
    }, [])
    const [getPacks, { data }] = useLazyQuery(Packs_Paginate, {
        fetchPolicy: 'no-cache',
        onCompleted: () => {
            setPacks(data.getPacks)
            setTotalFields(data.totalPacks)
        }
    })
    const handleChangePage = (event, newPage) => {
        setPage(newPage)
        getPacks({ variables: { start: newPage, end: rowsPerPage } })
    }
    const handleChangeRowsPerPage = event => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    }
    //Opens modals
    const onOpenDeletePack = id => () => {
        setPackSelected(id)
        setOpenDeletePack(true)
    }
    const onOpenRestorePack = id => () => {
        setPackSelected(id);
        setOpenRestorePack(true)
    }
    const onOpenEditPack = pack => () => {
        setPackSelected(pack)
        setOpenEditPack(true)
    }
    const onCreatePack = () => {
        setOpenCreatePack(true)
    }
    //Close modals
    const onCloseDeletePack = () => {
        setOpenDeletePack(false)
    }
    const onCloseRestorePack = () => {
        setOpenRestorePack(false)
    }
    const onCloseCreatePack = () => {
        setOpenCreatePack(false)
    }
    const onCloseEditPack = () => {
        setOpenEditPack(false)
    }
    //Actions dbs
    const onDeletePack = () => {
        setOpenDeletePack(false)

        deletePack({
            variables: { id: pack.id }
        }).then(response => {
            const newPack = packs.map((currentPack, _) => {
                if (pack.id !== currentPack.id)
                    return currentPack

                currentPack.status = false
                return currentPack
            })
            setPacks(newPack)
        })
    }

    //Actions dbs
    const onRestorePack = () => {
        setOpenRestorePack(false)

        restorePack({
            variables: { id: pack.id }
        }).then(response => {
            const newPacks = packs.map((currentPack, index) => {
                if (pack.id !== currentPack.id)
                    return currentPack

                currentPack.status = true
                return currentPack
            })
            setPacks(newPacks)
        })
    }

    //External actions
    const setPack = _ => {
        getPacks({ variables: { start: page, end: rowsPerPage } })
        setOpenCreatePack(false)
    }


    return (

        <ThemeProvider theme={theme} >
            <Fragment>
                <Paper className={classes.margin}>
                    <Fab onClick={onCreatePack} variant="extended" size="small" className={classes.margin}>
                        <AddIcon className={classes.extendedIcon} />
                                    Nuevo paquete
                                </Fab>
                    <TableContainer className={classes.container}>
                        <Table stickyHeader aria-label="sticky table">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Nombre</TableCell>
                                    <TableCell>Precio</TableCell>
                                    <TableCell>Fecha</TableCell>
                                    <TableCell colSpan={4}>Acciones</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {packs.map(row => {
                                    return (
                                        <TableRow hover role="checkbox" tabIndex={-1} key={row.id}>
                                            <TableCell>{row.name}</TableCell>
                                            <TableCell>{row.price}</TableCell>
                                            <TableCell>{row.date}</TableCell>

                                            <TableCell className={classes.iconWidth}>
                                                <Tooltip title="Editar" placement="top">
                                                    <EditIcon className={classes.icon} onClick={onOpenEditPack(row)} />
                                                </Tooltip>
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <Tooltip title="Eliminar" placement="top">
                                                    {
                                                        row.status ?
                                                            <DeleteIcon className={classes.icon} onClick={onOpenDeletePack(row)} />
                                                            :
                                                            <RestoreIcon className={classes.icon} onClick={onOpenRestorePack(row)} />
                                                    }
                                                </Tooltip>
                                            </TableCell>

                                        </TableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[10, 25, 100]}
                        component="div"
                        count={totalFields}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onChangePage={handleChangePage}
                        onChangeRowsPerPage={handleChangeRowsPerPage}
                    />
                </Paper>
                {/* Modal crear */}
                <Dialog open={openCreatePack} onClose={onOpenEditPack} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Nuevo paquete</DialogTitle>
                    <DialogContent>
                        <CreatePack onCloseCreatePack={onCloseCreatePack} setPack={setPack} modal={true} />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={onCloseCreatePack} color="primary">
                            Cerrar
                        </Button>
                    </DialogActions>
                </Dialog>

                {/* Modal editar */}
                <Dialog open={openEditPack} onClose={onCloseEditPack} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Editar paquete</DialogTitle>
                    <DialogContent>
                        <EditPack onCloseEditPack={onCloseEditPack} pack={pack} setPack={setPack} modal={true} />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={onCloseEditPack} color="primary">Cerrar</Button>
                    </DialogActions>
                </Dialog>

                {/* Modal eliminar */}
                <Dialog
                    open={openDeletePack}
                    onClose={onCloseDeletePack}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        Alerta
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Esta seguro de eliminar este paquete?
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button autoFocus onClick={onCloseDeletePack} color="primary">
                            NO
                        </Button>
                        <Button onClick={onDeletePack} color="primary">
                            SI
                        </Button>
                    </DialogActions>
                </Dialog>

                {/* Modal restaurar */}
                <Dialog
                    open={openRestorePack}
                    onClose={onCloseRestorePack}
                    aria-labelledby="draggable-dialog-title">
                    <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                        Alerta
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Esta seguro de recuperar este paquete?
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button autoFocus onClick={onCloseRestorePack} color="primary">
                            NO
                        </Button>
                        <Button onClick={onRestorePack} color="primary">
                            SI
                        </Button>
                    </DialogActions>
                </Dialog>
            </Fragment>
        </ThemeProvider >
    );
}