import { useQuery, useMutation } from "@apollo/react-hooks"
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import clsx from 'clsx';
import { Button } from '@material-ui/core';
import { DropzoneArea } from 'material-ui-dropzone'
import PropTypes from "prop-types";
import Select from "react-select";
import TextField from "@material-ui/core/TextField";
import Chip from "@material-ui/core/Chip";
import MenuItem from "@material-ui/core/MenuItem";
import CancelIcon from "@material-ui/icons/Cancel";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { Update_Pack } from '../../mutations/Pack/packs'
import FilterListIcon from '@material-ui/icons/FilterList'
import Typography from '@material-ui/core/Typography';
import FilterSearchProduct from './FilterSearchProduct'
import DeleteIcon from '@material-ui/icons/Delete';
import Tooltip from '@material-ui/core/Tooltip';
import Swal from 'sweetalert2'


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />
}
inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any.isRequired
    })
  ])
}
function Control(props) {
  const {
    children,
    innerProps,
    innerRef,
    selectProps: { classes, TextFieldProps }
  } = props;

  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: classes.input,
          ref: innerRef,
          children,
          ...innerProps
        }
      }}
      {...TextFieldProps}
    />
  );
}
function Option(props) {
  return (
    <MenuItem
      ref={props.innerRef}
      selected={props.isFocused}
      component="div"
      style={{
        fontWeight: props.isSelected ? 500 : 400
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  );
}
function MultiValue(props) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={clsx(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused
      })}
      onDelete={props.removeProps.onClick}
      deleteIcon={<CancelIcon {...props.removeProps} />}
    />
  );
}

const EditPack = props => {

  const [file, setFile] = useState({})
  const [name, setName] = useState(props.pack.name)
  const [price, setPrice] = useState(props.pack.price)
  const [quantity, setQuantityPack] = useState(props.pack.quantity ? props.pack.quantity : 0)
  const [products, setProducts] = useState([])
  const [openFilter, setOpenFilter] = useState(false)


  // Validations
  const [errorPackName, setErrorPackName] = useState(false)
  const [errorTextPackName, setErrorTextPackName] = useState('')

  const [errorPrice, setErrorPrice] = useState(false)
  const [errorTextPrice, setErrorTextPrice] = useState('')

  const [errorQuantityPack, setErrorQuantityPack] = useState(false)
  const [errorTextQuantityPack, setErrorTextQuantityPack] = useState('')

  const [updatePack] = useMutation(Update_Pack)
  const classes = useStyles()


  useEffect(() => {
    let newProducts = props.pack.pack_products.map(pack_product => {
      return {
        id: pack_product.product.id,
        name: pack_product.product.name,
        quantity: pack_product.quantity,
        price: pack_product.product.price,
        status: true
      }
    })
    console.log('newProducts', newProducts)
    setProducts(newProducts)
  }, [])

  const validarForm = () => {
    // if( values.password === ""  /*|| values.password_repeat == '' || values.password !== values.password_repeat*/ )
    //   setDisabled( false )
    // else
    //   setDisabled( true )
  }
  const [values, setValues] = useState({
    name: props.pack.name,
    price: props.pack.price,
    quantity: props.pack.quantity,
    pack: ''
  })
  const onChangeName = e => {
    setName(e.target.value)
    validatePackName(e.target.value)
  }
  const onChangePrice = e => {
    setPrice(e.target.value)
    validatePrice(e.target.value)
  }
  const onChangeQuantityPack = e => {
    setQuantityPack(e.target.value)
    validateQuantity(e.target.value)
  }
  const validateForm = () => {
    let errors = 0
    errors += validatePackName()
    errors += validatePrice()
    errors += validateQuantity()
    return errors
  }
  const validatePackName = (value) => {
    let text = value !== undefined ? value : name

    if (text == null || text.toString().trim() === '') {
      setErrorPackName(true)
      setErrorTextPackName('Este campo es requerido')
      return 1
    }
    else {
      setErrorPackName(false)
      setErrorTextPackName('')
      return 0
    }
  }
  const validatePrice = (value) => {
    let text = value !== undefined ? value : price

    if (text == null || text.toString().trim() === '') {
      setErrorPrice(true)
      setErrorTextPrice('Este campo es requerido')
      return 1
    }
    else if (Number(text) <= 0) {
      setErrorPrice(true)
      setErrorTextPrice('Valor no permitido')
      return 1
    }
    else {
      setErrorPrice(false)
      setErrorTextPrice('')
      return 0
    }
  }
  const validateQuantity = (value) => {
    let text = value !== undefined ? value : quantity

    if (text == null || text.toString().trim() === '') {
      setErrorQuantityPack(true)
      setErrorTextQuantityPack('Este campo es requerido')
      return 1
    }
    else if (Number(text) < 0) {
      setErrorQuantityPack(true)
      setErrorTextQuantityPack('Valor no permitido')
      return 1
    }
    else {
      setErrorQuantityPack(false)
      setErrorTextQuantityPack('')
      return 0
    }
  }
  const handlePicture = file => {
    setFile(file[0])
  }
  const handleFilterOpen = () => {
    setOpenFilter(true);
  }
  const getProducts = () => {
    let ids = products.map(product => {
      return product.id
    })
    return ids
  }
  const onDeleteProduct = (product) => () => {
    let newProducts = products.filter(obj => obj !== product)
    setProducts(newProducts)

  }
  const handleFilterClose = () => {
    setOpenFilter(false);
  }
  const selectProduct = (product) => {
    product.quantity = 0
    setProducts(products => [...products, product])
  }
  const onChangeQuantity = _index => e => {
    let priceTotal = 0
    const newProducts = products.map((product, index) => {

      if (_index !== index)
        return product

      product.quantity = Number(e.target.value)

      return product
    })

    newProducts.map((product, _) => {
      console.log('product', product)
      priceTotal += Number(product.price) * Number(product.quantity)
    })
    setProducts(newProducts)
    setPrice(priceTotal)
  }
  const onSubmitPack = e => {
    let newPackProducts = products.map((product, _) => {
      return {
        id: props.pack.id,
        quantity: product.quantity,
        product: product.id
      }
    })

    const input =
    {
      id: props.pack.id,
      name: name,
      price: parseFloat(price),
      quantity: parseFloat(quantity),
      pack_products: newPackProducts
    }
    updatePack({
      variables: { input: input, file: file }
    }).then(response => {
      console.log(response)
      console.log(props)

      if (response.data.updatePack) {

        if (props.modal) {
          Swal.fire({
            icon: 'success',
            title: `El paquete fue actualizado exitosamente`,
            showConfirmButton: false,
            timer: 1500
          })
          props.setPack(response.data.updateProduct)
          props.onCloseEditPack()
        }
      }
    })
  }


  return (
    <Fragment>

      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmitPack()
            }} >
            <Grid container spacing={3}>
              <Grid item xs={6}>
                <TextField fullWidth
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre"
                  value={name}
                  onChange={onChangeName} />
              </Grid>

              <Grid item xs={6} >
                <TextField fullWidth
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Precio"
                  value={price}
                  onChange={onChangePrice} />
              </Grid>

              <Grid item xs={12} sm={6}>
                <TextField fullWidth
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Cantidad"
                  value={quantity}
                  onChange={onChangeQuantityPack} />
              </Grid>

              <Grid item xs={6}>
                <Button
                  className={classes.filterButton}
                  color="primary"
                  onClick={handleFilterOpen}
                  size="small"
                  variant="outlined" >
                  <FilterListIcon className={classes.filterIcon} /> Agregar productos
                </Button>
                {
                  products.length === 0 ?
                    <div className={classes.error}><Typography>No se han agregado productos</Typography></div>
                    : ''
                }
              </Grid>

              <Grid item xs={12} >
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>Producto</TableCell>
                      <TableCell>Precio</TableCell>
                      <TableCell style={{ width: 160 }}>Cantidad</TableCell>
                      <TableCell>Eliminar</TableCell>

                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {

                      products.map((product, index) => (
                        <TableRow key={index}>

                          <TableCell>{product.name}</TableCell>
                          <TableCell>{product.price}</TableCell>
                          <TableCell>
                            <TextField
                              autoComplete='off'
                              id="outlined-simple-start-adornment"
                              className={clsx(classes.margin, classes.textField)}
                              variant="outlined"
                              label="Cantidad"
                              margin="dense"
                              value={product.quantity}
                              onChange={onChangeQuantity(index)} />
                          </TableCell>
                          <TableCell className={classes.iconWidth}>
                            <Tooltip title="Editar" placement="top">
                              <DeleteIcon className={classes.icon} onClick={onDeleteProduct(product)} />
                            </Tooltip>
                          </TableCell>

                        </TableRow>
                      ))
                    }
                  </TableBody>
                </Table>
              </Grid>


              <Grid item xs={12} sm={12}>
                <DropzoneArea
                  open={true}
                  filesLimit={1}
                  dropzoneText="Arrastre y suelte un archivo de imagen aquí o haga clic"
                  acceptedFiles={['image/jpeg', 'image/png', 'image/bmp']}
                  onChange={handlePicture} />

              </Grid>


              <Grid item xs={2} sm={3}>
                <Button /*disabled={disabled} */ type="submit" variant="contained" color='primary'>Actualizar</Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
      <FilterSearchProduct
        onClose={handleFilterClose}
        onFilter={null}
        open={openFilter}
        getProducts={getProducts}
        selectProduct={selectProduct} />
    </Fragment>
  )
}

export default EditPack